<template>
  <main>
    <AdminNav />

    <div class="container">
      <div class="card mx-auto shadow mb-4" style="width: 40rem;">
        <div class="card-body">
          <h5 class="card-title mb-2">Show students Result</h5>
          <ul class="list-group" v-for="le in result" :key="le.Id">
            <li class="list-group-item">
              {{ le.Fname }} {{ le.Lname }} {{ le.Oname }}
              {{ le.course_title }}  {{ le.course_code }}
              <b> Test </b> {{ le.test_score }} <b> Practical </b>
              {{ le.practical_score }} <b> Exam </b> {{ le.exam_score }}
              <b> Total </b> {{ le.Total_mark }} <b> Grade </b> {{ le.Grade }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import AdminNav from '@/components/AdminUI/navbar.vue'
import axios from 'axios'
export default {
  name: 'showResults',
  components: {
    AdminNav
  },
  data () {
    return {
      result: []
    }
  },
  mounted () {
    this.getResults()
  },
  methods: {
    getResults () {
      axios.get('http://localhost:3000/api/result/getResults').then(res => {
        this.result = res.data
		console.log(res.data)
      })
    }
  }
}
</script>

