<template>
  <main>
    <DashNav />
    <div class="card shadow mx-auto" style="width: 40rem;">
      <div class="card-body">
        <h5 class="card-title text-center"> Registered courses </h5>
        <ul class="list-group" v-for="cos in course" :key="cos.Id">
          <li class="list-group-item">
            <b> name </b> {{ cos.names }} <b> Course Registered </b>
            {{ cos.cos }} <b> level </b> {{ cos.levels }}
          </li>
        </ul>
      </div>
    </div>
  </main>
</template>

<script>
import axios from 'axios'
import DashNav from '@/components/UserUI/DashNav.vue'
export default {
  name: 'studentRegister',
  components: {
    DashNav
  },
  data () {
    return {
      course: []
    }
  },
  mounted () {
    this.getRegisteredcourses()
  },
  methods: {
    getRegisteredcourses () {
      let Id = localStorage.getItem('Id')
      let data = new Object()
      data.Id = Id

      axios
        .post('http://localhost:3000/api/studentResult/courseregister', data)
        .then(res => {
          this.course = res.data
        })
    }
  }
}
</script>

