<template>
  <main>
    <AdminNav />

    <div class="container">
      <div class="card mx-auto shadow-lg" style="width: 40rem;">
        <div class="card-body">
          <h5 class="card-title mb-5">
            {{ courseTitle }} result input for {{ name }} {{ lname }}
          </h5>

          <div v-if="errors.length" class="alert alert-success" role="alert">
            <span class="text-center " v-for="error in errors">{{
              error
            }}</span>
          </div>

          <div v-if="success.length" class="alert alert-success " role="alert">
            <span class="text-center " v-for="suc in success">{{ suc }}</span>
          </div>

          <div v-if="resError.length" class="alert alert-danger " role="alert">
            <span class="text-center " v-for="err in resError">{{ err }}</span>
          </div>

          <form>
            <label> Course </label>
            <input
              class="form-control mb-3"
              type="text"
              v-model="form.course"
              aria-label="Disabled input example"
            />
            <label> Enter practical score </label>
            <input
              v-model="form.practical"
              class="form-control mb-3"
              placeholder="practical score"
              type="number"
            />
            <label> Enter test score </label>
            <input
              v-model="form.test"
              class="form-control mb-3"
              placeholder="test score"
              type="number"
            />
            <label> Enter Exam score </label>
            <input
              v-model="form.exam"
              class="form-control mb-3"
              placeholder="exam score"
              type="number"
            />

            <input v-model="form.courseId" hidden />

            <input v-model="form.studentId" hidden />

            <button @click="submit" type="submit" class="btn btn-primary">
              Submit
            </button>
          </form>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import AdminNav from '@/components/AdminUI/navbar.vue'
import axios from 'axios'
axios.defaults.crossDomain = true
export default {
  name: 'resultinput',
  components: {
    AdminNav
  },
  data () {
    return {
      name: '',
      lname: '',
      courseId: '',
      studentId: '',
      courseTitle: '',
      form: {
        course: '',
        test: '',
        exam: '',
        practical: '',
        studentId: '',
        courseId: ''
      },
      errors: [],
      success: [],
      resError: []
    }
  },
  mounted () {
    if (
      this.$route.query.courseId === undefined ||
      this.$route.query.studentId === undefined
    ) {
      this.$router.push('/result')
    }
    this.name = this.$route.query.stuFname
    this.lname = this.$route.query.stuLname
    this.courseTitle = this.$route.query.coursename
    this.form.course = this.courseTitle

    this.studentId = this.$route.query.studentId
    this.courseId = this.$route.query.courseId

    this.form.studentId = this.studentId
    this.form.courseId = this.courseId
  },

  methods: {
    submit (e) {
      e.preventDefault(e)
      if (
        (this.form.test > 30 &&
          this.form.practical > 20 &&
          this.form.exam > 100) ||
        (this.form.test == '' &&
          this.form.exam == '' &&
          this.form.practical == '')
      ) {
        this.errors.push('Please check your inputs...')
      } else {
        axios
          .post('http://localhost:3000/api/result/inputResult', this.form)
          .then(res => {
            if (res.data === 200) {
              this.success.push("Student's result has been uploaded ")
              setTimeout(() => {
                location.reload()
              }, 3000)
            } else if (res.data === 501) {
              this.errors.push('Please check your inputs...')
              setTimeout(() => {
                location.reload()
              }, 3000)
            } else if (res.data === 500) {
              this.resError.push('This result has been uploaded')
              setTimeout(() => {
                location.reload()
              }, 3000)
            }
          })
      }
    }
  }
}
</script>

<style></style>
