<template>
  <main>
    <DashNav />
    <div class="card shadow mx-auto" style="width: 38rem;">
      <div class="card-body">
        <h5 class="card-title">Results</h5>
        <ul class="list-group" v-for="resp in result" :key="resp.student_Id">
          <li class="list-group-item">
            {{ resp.names }} <b> course registered </b> {{ resp.cos }}
            <b>Test Score </b> {{ resp.test_score }} <b> Practical score </b>
            {{ resp.practical_score }} <b> Exam score </b>
            {{ resp.exam_score }} <b> Total score </b> {{ resp.Total_mark }}
            <b> Grade </b> {{ resp.Grade }}
          </li>
        </ul>
      </div>
    </div>
  </main>
</template>

<script>
import DashNav from '@/components/UserUI/DashNav.vue'
import axios from 'axios'
export default {
  name: 'studentResult',
  components: {
    DashNav
  },
  data () {
    return {
      result: []
    }
  },
  mounted () {
    this.getResult()
  },
  methods: {
    getResult () {
      let Id = localStorage.getItem('Id')
      let data = new Object()
      data.Id = Id

      axios.post('http://localhost:3000/api/studentResult', data).then(res => {
        this.result = res.data
      })
    }
  }
}
</script>

