<template>
  <main>
    <AdminNav />

    <div class="container">
      <div class="card shadow-lg mx-auto" style="width: 55rem;">
        <div class="card-body">
          <h5 class="card-title">Students who registered for courses</h5>
          <ul class="list-group" v-for="student in students" :key="student.Id">
            <li class="list-group-item mb-2">
              <strong> Student's name </strong> {{ student.Fname }}
              {{ student.Lname }} {{ student.Oname }}  &nbsp;

              <strong> Course registered </strong> {{ student.course_code }}
              {{ student.course_title }}  <strong> Session </strong
			  > {{student.session}}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from 'axios'
import AdminNav from '@/components/AdminUI/navbar.vue'
export default {
  name: 'registeredStudents',
  components: {
    AdminNav
  },
  data () {
    return {
      students: []
    }
  },
  mounted () {
    this.fetchRegistered()
  },
  methods: {
    fetchRegistered () {
      axios.get('http://localhost:3000/api/result/showRegistered').then(res => {
        this.students = res.data
      })
    }
  }
}
</script>

